export default {
	data() {
		return {
			title: 'Hello',
			disabled: false,
			btnTitle: "验证码",
			txt: '',
			userInfo: {
				phone: '',
				captcha: '',
				password: '',
				passwordComfirm: ''
			}
		}
	},
	onLoad() {

	},
	methods: {
		codeClick() {
		      //点击发送验证码		    
		      let _this=this
		      if(!this.userInfo.phone){
		        uni.showToast({
		        	title: '请输入手机号',
		        	icon: 'none'
		        })
		        return
		      }
		      this.disabled=true
		      this.$apiYZX.captcha({phone:this.userInfo.phone,type:'3'}).then(res=>{
		         if(res.data.code==200){
		          this.btnTitle=60
		        let _this=this
		        this.txt='S秒后获取'
		        let timer=setInterval(function(){
		          if(_this.btnTitle==1){
		             clearInterval(timer)
		            _this.btnTitle='获取验证码'
		            _this.txt=''
		            _this.disabled=false      
		          }else{
		             _this.btnTitle=_this.btnTitle-1
		          }     
		        },1000)
		       }else{
		         this.disabled=false
		       }
		      })
		    },
			test(){
				if(!this.userInfo.phone || !this.userInfo.captcha || !this.userInfo.password || !this.userInfo
					.passwordComfirm) {		
					uni.showToast({
						title: '请输入完整信息',
						icon: 'none'
					})
					return false
				}
				if(this.userInfo.passwordComfirm!==this.userInfo.password){
					uni.showToast({
						title: '输入的密码不一致',
						icon: 'none'
					})
					return false
				}
				return true
			},
		    modifyFunc() {
		         if(!this.test()) return false
		         this.$apiYZX.modify(this.userInfo).then(res => {
		         	if(res.data.code=='200'){
		         		uni.showToast({
		         		    title: '重置成功',
		         			icon:'success',
		         			success() {
		         				uni.navigateTo({
		         				    url: 'login'
		         				});
		         			}
		         		})	
		         	}
		         })
		     
		    }
		
	}
}
